.main-footer__contacts {
  background-color: $color-alizarin-red;
}

.main-footer__contacts-inner {
  display: flex;
  align-items: center;
  max-width: 1920px;
  min-height: 86px;
  margin: 0 auto;
  padding: 0 60px;

  @include vp-1919 {
    min-height: 56px;
  }

  @include vp-1279 {
    padding: 0 30px;
  }

  @include vp-767 {
    padding: 30px 15px;
    flex-direction: column;
    align-items: flex-start;
    min-height: 227px;
  }
}

.main-footer__contacts a,
.main-footer__contacts p,
.main-footer__contacts span {
  color: $color-default-white;
}

.main-footer__contacts p {
  margin: 0;
}

.main-footer__logo {
  margin-right: 200px;

  @include vp-1919 {
    margin-right: 150px;
  }

  @include vp-1279 {
    margin-right: 110px;
  }

  @include vp-1023 {
    margin-right: 60px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

.main-footer__logo img {
  @include vp-1919 {
    width: 140px;
    height: 17px;
  }

  @include vp-1023 {
    width: 120px;
    height: 15px;
  }
}

.main-footer__contacts-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-767 {
    flex-direction: column;
  }
}

.main-footer__contacts-item:not(:last-of-type) {
  margin: 5px 60px 5px 0;

  @include vp-1919 {
    margin-right: 80px;
  }

  @include vp-1279 {
    margin-right: 60px;
  }

  @include vp-1023 {
    margin-right: 30px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: -5px;
  }
}

.main-footer__phone,
.main-footer__email {
  display: flex;

  @include vp-767 {
    margin-bottom: 15px;
  }
}

.main-footer__phone {
  @include vp-767 {
    margin-left: 3px;
  }

  &:hover svg,
  &:hover span,
  &:hover p {
    color: $color-aluminum;
    fill: $color-aluminum;
  }

  &:active svg,
  &:active span,
  &:active p {
    color: $color-zambezi;
    fill: $color-zambezi;
  }
}

.main-footer__phone svg {
  margin: 5px 10px 0 0;
  fill: $color-default-white;
  transition: fill 0.6s ease;

  @include vp-767 {
    margin-right: 14px;
  }
}

.main-footer__phone-box span {
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  transition: color 0.6s ease;

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
  }
}

.main-footer__phone-box p {
  font-size: 14px;
  line-height: 14px;
  transition: color 0.6s ease;

  @include vp-1919 {
    font-size: 12px;
    line-height: 12px;
  }

  @include vp-1023 {
    font-size: 10px;
    line-height: 10px;
  }
}

.main-footer__email {

  &:hover svg,
  &:hover span,
  &:hover p {
    color: $color-aluminum;
    fill: $color-aluminum;
  }

  &:active svg,
  &:active span,
  &:active p {
    color: $color-zambezi;
    fill: $color-zambezi;
  }
}

.main-footer__email p {
  font-size: 14px;
  line-height: 14px;
  transition: color 0.6s ease;

  @include vp-1919 {
    font-size: 12px;
    line-height: 12px;
  }

  @include vp-1023 {
    font-size: 10px;
    line-height: 10px;
  }
}

.main-footer__email svg {
  margin: 5px 8px 0 0;
  fill: $color-default-white;
  transition: fill 0.6s ease;

  @include vp-1919 {
    margin-top: 9px;
  }
}

.main-footer__email span {
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;
  transition: color 0.6s ease;

  @include vp-1919 {
    font-size: 14px;
    line-height: 18px;
  }

  @include vp-1023 {
    font-size: 12px;
  }
}

.main-footer__email-box {
  @include vp-1919 {
    padding-top: 2px;
  }
}

.main-footer__main-section {
  background-color: $color-night-rider;
}

.main-footer__main-section-inner {
  display: flex;
  max-width: 1920px;
  margin: 0 auto;

  @include vp-1919 {
    flex-direction: column;
  }
}

.main-footer__main-section-inner p,
.main-footer__main-section-inner span,
.main-footer__main-section-inner a {
  color: $color-default-white;
}


.main-footer__local-office {
  width: 447px;
  padding: 30px 60px 38px 60px;
  box-sizing: border-box;

  @include vp-1919 {
    display: flex;
    width: auto;
    min-height: 90px;
    padding: 15px 60px;
    border-bottom: 2px solid $color-zambezi;
  }

  @include vp-1279 {
    padding: 15px 30px;
  }

  @include vp-1023 {
    min-height: 84px;
  }

  @include vp-767 {
    flex-direction: column;
    padding: 30px 15px;
  }
}

.main-footer__local-office p {
  margin: 0;
}

.main-footer__location {
  margin-bottom: 27px;

  @include vp-1919 {
    margin-right: 150px;
    margin-bottom: 0;
  }

  @include vp-1279 {
    margin-right: 159px;
  }

  @include vp-1023 {
    margin-right: 88px;
  }

  @include vp-767 {
    margin-bottom: 27px;
    margin-right: 0;
  }
}

.main-footer__address {
  margin-bottom: 27px;

  @include vp-1919 {
    margin-right: 60px;
    margin-bottom: 0;
    width: 384px;
  }

  @include vp-1279 {
    margin-right: 67px;
  }

  @include vp-1023 {
    width: 300px;
    margin-right: 30px;
  }

  @include vp-767 {
    margin-bottom: 27px;
    margin-right: 0;
  }
}

.main-footer__location p,
.main-footer__address span,
.main-footer__work-time span {
  font-size: 14px;
  line-height: 17px;
}

.main-footer__location span,
.main-footer__work-time p {
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
}

.main-footer__location span {
  border-bottom: 2px dashed $color-alizarin-red;
  transition: color 0.6s ease;
  cursor: pointer;

  @include vp-1919 {
    font-size: 16px;
  }

  @include vp-1023 {
    font-size: 14px;
  }

  &:hover {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}

.main-footer__location svg {
  color: $color-alizarin-red;
}

.main-footer__address p {
  margin-top: -5px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}

.main-footer__work-time p {
  margin-top: -4px;

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}

.main-footer__nav {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  @include vp-1023 {
    grid-template-columns: 1fr 1fr;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.main-footer__list-wrapper {
  padding: 30px;
  box-sizing: border-box;
  border-left: 2px solid $color-zambezi;

  @include vp-1919 {
    &:first-of-type {
      padding-left: 60px;
    }

    &:last-of-type {
      padding-right: 60px;
    }
  }

  @include vp-1279 {
    &:first-of-type {
      padding-left: 30px;
    }

    &:last-of-type {
      padding-right: 30px;
    }
  }

  @include vp-1023 {
    padding-bottom: 26px;

    &:nth-of-type(odd) {
      border-left: none;
    }

    &:not(:nth-last-of-type(-n + 2)) {
      border-bottom: 2px solid $color-zambezi;
    }
  }

  @include vp-767 {
    padding: 30px 15px 24px 15px;
    border-left: none;

    &:first-of-type {
      padding-left: 15px;
    }

    &:not(:last-of-type) {
      border-bottom: 2px solid $color-zambezi;
    }
  }

  &--catalog {
    @include vp-767 {
      order: 2;
    }
  }

  &--to-partners {
    @include vp-767 {
      order: 4;
    }
  }

  &--about {
    @include vp-767 {
      order: 3;
    }
  }

  &--additional {
    @include vp-767 {
      order: 5;
    }
  }
}

.main-footer__nav-list,
.main-footer__additional-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-footer__list-wrapper h3 {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: "TTAutonomous", "Arial", sans-serif;
  font-size: 20px;
  line-height: 28px;
  font-weight: 800;
  color: $color-default-white;
  text-transform: uppercase;

  @include vp-1919 {
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 25px;
  }
}

.main-footer__nav-item:not(:last-of-type) {
  margin-bottom: 15px;

  @include vp-1919 {
    margin-bottom: 11px;
  }

  @include vp-1023 {
    margin-bottom: 7px;
  }
}

.main-footer__additional-list-item:not(:last-of-type) {
  margin-bottom: 25px;

  @include vp-1919 {
    margin-bottom: 21px;
  }

  @include vp-1023 {
    margin-bottom: 7px;
  }
}

.main-footer__nav-item a,
.main-footer__additional-list a {
  font-size: 18px;
  line-height: 22px;
  text-decoration: underline;
  transition: color 0.6s ease;

  &:hover,
  &:focus {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}

.main-footer__policy {
  background-color: $color-default-black;
}

.main-footer__policy-inner {
  display: flex;
  align-items: center;
  max-width: 1920px;
  min-height: 90px;
  margin: 0 auto;
  padding: 0 60px;

  @include vp-1919 {
    justify-content: space-between;
    min-height: 74px;
  }

  @include vp-1279 {
    min-height: 60px;
    padding: 0 30px;
  }

  @include vp-1023 {
    flex-direction: column;
    min-height: 130px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  @include vp-767 {
    padding: 15px;
  }
}

.main-footer__policy-inner a,
.main-footer__policy-inner p {
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  color: $color-default-white;

  @include vp-1279 {
    font-size: 12px;
    line-height: 15px;
  }
}

.main-footer__policy-inner > p {
  width: 214px;
  margin: 0 262px 10px 314px;

  @include vp-1919 {
    margin: 0;
    width: 230px;
  }

  @include vp-1439 {
    margin-right: 125px;
  }

  @include vp-1279 {
    width: 180px;
    margin-right: 80px;
  }

  @include vp-1023 {
    width: auto;
    margin-right: 0;
    margin-bottom: 20px;
  }

  @include vp-767 {
    text-align: center;
  }
}

.main-footer__policy-inner a {
  text-decoration: underline;
  margin-right: 100px;
  margin-bottom: 10px;
  transition: color 0.6s ease;

  @include vp-1919 {
    margin-right: 0;
  }

  @include vp-1023 {
    margin-bottom: 20px;
  }

  &:hover {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}

.main-footer__develop {
  display: flex;
}

.main-footer__develop-img-container {
  line-height: 0;
  margin-right: 12px;

  @include vp-767 {
    margin-right: 8px;
  }
}

.main-footer__develop-img-container img {
  @include vp-1279 {
    width: 70px;
    height: 30px;
  }
}

.main-footer__developer p {
  margin: 0;
}

.main-footer__developer a {
  transition: color 0.6s ease;

  @include vp-1023 {
    display: block;
    margin-bottom: 0;
  }

  &:hover {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}
