.promo {
  position: relative;
  margin: 0 auto;
  width: 100%;
  overflow: hidden;
  margin-bottom: 79px;

  @include vp-1919 {
    margin-bottom: 57px;
  }

  @include vp-1279 {
    margin-bottom: 36px;
  }

  @include vp-1023 {
    margin-bottom: 64px;
  }

  @include vp-767 {
    margin-bottom: 27px;
  }

  &__slider {
    overflow: hidden;
    background-color: $color-night-rider;
  }

  &__slides {
    display: flex;

    // min-height: 780px;

    @include vp-1919 {
      // min-height: 650px;
    }
  }

  &__slide {
    flex: 0 0 100%;
    display: flex;

    @include vp-1439 {
      // min-height: 650px;
    }
  }

  &__slide-img {
    min-height: 780px;

    @include vp-1919 {
      min-height: 650px;
    }

    @include vp-1439 {
      min-height: 578px;
    }

    @include vp-1279 {
      min-height: 446px;
    }

    @include vp-767 {
      min-height: 301px;
    }

    img {
      position: absolute;
      top: 50%;
      // margin: 0 auto;
      width: 100%;
      height: 100%;
      object-fit: cover;

      transform: translateY(-50%);
    }
  }

  &__slide-icon {
    position: absolute;
    right: 100px;
    top: 18px;
    width: 550px;
    height: 550px;

    @include vp-1919 {
      right: 171px;
      top: 23px;

      width: 409px;
      height: 430px;
    }

    @include vp-1439 {
      width: 339px;
      height: 360px;
    }

    @include vp-1279 {
      width: 286px;
      height: 300px;
      right: 140px;
    }

    @include vp-1023 {
      right: -60px;
      top: 40px;
    }

    @include vp-767 {
      display: none;
    }

    img {
      position: absolute;
      width: 100%;
      // height: 100%;
      object-fit: contain;
    }
  }

  &__slide-inner {
    position: relative;
    display: flex;
  }

  &__slide-content {
    position: relative;
    width: 100%;
    // max-width: 1920px;
    max-width: 1577px;
    color: $color-default-white;
    margin-top: 176px;
    margin-left: auto;
    padding-bottom: 125px;

    @include vp-1919 {
      max-width: 1320px;
      margin: 124px auto 0 30px;
    }

    @include vp-1439 {
      max-width: 1160px;
      margin-top: 100px;
    }

    @include vp-1279 {
      margin-top: 60px;
      margin-left: 0;
    }

    @include vp-767 {
      margin-top: 30px;
    }

    .title--h3 {
      max-width: min-content;
      margin-bottom: 15px;

      @include vp-1919 {
        margin-bottom: 9px;
      }

      @include vp-767 {
        max-width: 100%;
        margin-bottom: 15px;
      }
    }

    .link {
      // margin-bottom: 20px;
      padding: 14px 30px;
      font-size: 18px;
      line-height: 16px;
      background-color: $color-alizarin-red;
      transition: background-color $trans-600;

      &--white {
        @media (hover: hover), screen and (min-width: 0\0) {
          &:hover,
          &:focus {
            color: $color-alizarin-red;
            background-color: $color-default-white;
            }
          }
        }

      @include vp-1279 {
        padding: 9px 14px;
        font-size: 14px;
      }

      @include vp-767 {
        padding: 5px 10px;
        font-size: 12px;
      }

      svg {
        margin-left: 10px;

        @include vp-1279 {
          width: 30px;
          margin-left: 13px;
        }

        @include vp-767 {
          width: 23px;
        }
      }
    }
  }

  &__slide-description {
    max-width: 616px;
    margin: 0;
    margin-bottom: 40px;

    font-weight: 800;
    font-size: 30px;
    line-height: 45px;
    font-family: $ff-ttautonomous;
    text-transform: uppercase;

    @include vp-1919 {
      margin-bottom: 30px;

      font-size: 24px;
      line-height: 30px;
    }

    @include vp-1279 {
      max-width: 600px;
      margin-bottom: 22px;
      font-size: 20px;
      line-height: 24px;
    }

    @include vp-767 {
      max-width: 100%;
      margin-bottom: 15px;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__slide-text {
    max-width: 577px;
    margin: 0;
    margin-bottom: 40px;
    font-weight: 600;
    font-size: 28px;
    line-height: 36px;

    @include vp-1919 {
      max-width: 400px;
      margin-bottom: 30px;

      font-size: 20px;
      line-height: 28px;
    }

    @include vp-1279 {
      max-width: 370px;
      margin-bottom: 18px;
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      max-width: 100%;
      margin-bottom: 15px;
      font-size: 14px;
      line-height: 18px;
    }
  }

  &__pagination {
    position: absolute;
    bottom: 180px;
    left: calc(100vw - 1599px);

    @include vp-1919 {
      left: 60px;
      bottom: 196px;
    }

    @include vp-1439 {
      bottom: 177px;
    }

    @include vp-1279 {
      bottom: 212px;
      left: 33px;
    }

    @include vp-1023 {
      left: 33px;
      bottom: 324px;
    }

    @include vp-767 {
      left: 15px;
      // bottom: 595px;
    }

    @include vp-639 {
      bottom: 595px;
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 100vw;
      height: 2px;

      background-color: $color-default-white;
    }

    &:before {
      left: 130px;

      @include vp-1279 {
        left: 100px;
      }
    }

    &:after {
      right: 130px;

      @include vp-1279 {
        right: 100px;
      }
    }
  }

  &__cards {
    // position: relative;
    // z-index: 1;
    width: calc(100% - 8px);
    display: flex;
    justify-content: flex-end;
    margin: -282px 0 33px 0;
    padding: 0;

    list-style: none;

    @include vp-1919 {
      width: calc(100% + 15px);
      margin-top: -235px;
    }

    @include vp-1439 {
      width: calc(100% - 15px);
      margin-top: -225px;
    }

    @include vp-1279 {
      width: calc(100% + 12px);
      margin: -144px 0 17px 0;
    }

    @include vp-1023 {
      margin: -32px 0 17px -9px;
      justify-content: flex-start;
    }

    @include vp-767 {
      width: 100%;
      margin: -30px auto 17px;
      justify-content: center;
    }

    @include vp-639 {
      flex-wrap: wrap;
      margin: 29px auto 15px;
      justify-content: flex-start;
    }
  }

  // &__cards-item {
  //   // position: relative;
  //   display: flex;
  //   flex-direction: column;
  //   width: 100%;
  //   max-width: 406px;
  //   margin: 0 15px 0;
  //   transition: opacity $trans-600;

    .card {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: 406px;
      margin: 0 15px 0;
      transition: opacity $trans-600;
      min-height: 350px;
      padding: 20px;
      filter: drop-shadow(4px 4px 0px rgba(0, 0, 0, 0.25));

      @include vp-1919 {
        max-width: 320px;
        min-height: 284px;
        padding: 15px;
      }

      @include vp-1279 {
        max-width: 290px;
        min-height: 255px;
      }

      @include vp-767 {
        min-height: 240px;
      }

      @include vp-639 {
          max-width: 100%;
          margin: 0 0 16px;

          &:last-child {
            margin-bottom: 0;
          }
        }

      &__text {
        max-width: 345px;
      }

      &--red {
        color: $color-default-white;

        .link {
          transition: color $trans-600;
          &--white {
            @media (hover: hover), screen and (min-width: 0\0) {
              &:hover,
              &:focus {
                color: $color-dark-gray;
              }
            }
          }
        }
      }

      &--grey {
        .card__info {
          color: $color-dark-gray;
        }

        .card__text {
          color: $color-zambezi;
        }
      }

      &__info {
        margin-left: auto;
        margin-bottom: 20px;

        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;

        @include vp-1919 {
          margin-bottom: 15px;
        }

        @include vp-1279 {
          font-size: 12px;
          line-height: 15px;
        }
      }

      &__inner {
        display: flex;
        flex-direction: column;
        height: 100%;
      }

      &__content-wrapper {
        display: flex;
        flex-direction: column;
        height: inherit;
      }

      &__text {
        flex: 1 1 auto;
      }

      &__date {
        margin-top: 10px;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;

        color: $color-dark-gray;

        @include vp-1919 {
          margin-top: 22px;
          font-size: 14px;
          line-height: 17px;
        }

        @include vp-1279 {
          margin-top: 13px;
          font-size: 12px;
          line-height: 15px;
        }
      }

      &__text {
        margin: 0;
        margin-top: 20px;

        font-size: 18px;
        line-height: 22px;

        @include vp-1919 {
          margin-top: 13px;

          font-size: 16px;
          line-height: 20px;
        }

        @include vp-1279 {
          max-width: 250px;
          margin-top: 15px;
          font-size: 14px;
          line-height: 17px;
        }

        @include vp-639 {
          max-width: 87%;
        }
      }

      &__text-info {
        // display: block;
        font-weight: 700;
      }

      .link {
        margin-left: auto;

        @include vp-1919 {
          font-size: 14px;
          line-height: 17px;
        }

        svg {
          margin-left: 5px;
        }
      }
    }
  // }

  &__link {
    margin-right: 22px;
    text-align: right;

    @include vp-1919 {
      margin-right: 0;
    }

    @include vp-1439 {
      margin-right: 30px;
    }

    @include vp-1279 {
      margin-right: 0;
    }

    @include vp-1023 {
      text-align: right;
      width: 100%;
      max-width: 619px;
    }

    @include vp-767 {
      text-align: right;
      width: 100%;
      max-width: 100%;
      // margin-right: auto;
    }

    .link {
      font-size: 20px;
      line-height: 24px;

      @include vp-1919 {
        font-size: 18px;
        line-height: 22px;
      }

      @include vp-1279 {
        font-size: 14px;
        line-height: 17px;
      }
    }

    cursor: pointer;

    svg {
      margin-left: 10px;
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
  }
}
