.product-item {
  max-width: 580px;
  padding: 30px 20px;

  border: 1px solid $color-whisper;

  &__title {
    margin-bottom: 18px;

    font-size: 24px;
    line-height: 32px;

    @include vp-1023 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-bottom: 15px;
    padding-right: 15px;

    display: flex;
    align-items: center;
    justify-content: flex-start;

    font-size: 14px;
    line-height: 18px;

    svg {
      margin-right: 10px;
    }
  }

  &__link {
    text-decoration: underline;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
      }
    }
  }

  &--documents .product-item__item:last-child {
    margin-bottom: 0;
  }

  &--solutions .product-item__list {
    display: flex;
    flex-wrap: wrap;
  }

  &--solutions svg {
    width: 7px;
    height: 12px;

    color: $color-alizarin-red;
  }
}
