.main-header {
  border-bottom: 1px solid $color-whisper;
}

.main-header .container {
  padding: 28px 60px 38px;

  @include vp-1919 {
    padding-top: 31px;
    padding-bottom: 23px;
  }

  @include vp-1439 {
    padding: 30px 30px 29px;
  }

  @include vp-1023 {
    padding: 30px 29px 29px;
  }

  @include vp-767 {
    padding: 15px 15px 14px;
  }
}

.breadcrumbs {
  margin-bottom: 40px;

  @include vp-1439 {
    margin-bottom: 31px;
  }

  @include vp-767 {
    margin-bottom: 15px;
  }
}

.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  @include vp-1439 {
    font-size: 14px;
    line-height: 18px;
  }
}

.breadcrumbs__item {
  position: relative;

  &:not(:last-child) {
    margin-right: 39px;
  }

  &:not(:last-child)::after {
    content: "";
    position: absolute;
    top: 10px;
    right: -29px;
    width: 20px;
    height: 1px;
    background-color: $color-zambezi;
  }

  @include vp-1439 {
    &:not(:last-child) {
      margin-right: 41px;
    }
  }

  @include vp-1023 {
    &:not(:last-child) {
      margin-right: 45px;
    }

    &:not(:last-child)::after {
      top: 11px;
      right: -30px;
    }
  }

  @include vp-767 {
    margin-bottom: 3px;

    &:not(:last-child) {
      margin-right: 41px;
    }

    &:not(:last-child)::after {
      top: 9px;
    }
  }
}

.breadcrumbs__link {
  position: relative;
  color: $color-default-black;
  outline: none;
  transition: opacity $trans-600;

  &::before {
    position: absolute;
    bottom: 1px;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $color-default-black;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }

  &--current {
    color: $color-zambezi;

    &::before {
      display: none;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        opacity: 1;
      }
    }
  }

  @include vp-1439 {
    &::before {
      bottom: 2px;
    }
  }
}

.main-header__title {
  margin-bottom: 20px;

  @include vp-1919 {
    margin-bottom: 19px;
  }

  @include vp-1439 {
    margin-bottom: 15px;
  }

  @include vp-767 {
    margin-bottom: 16px;
    font-size: 22px;
  }
}

.main-header__description {
  max-width: 580px;
  margin: 0 0 41px;
  padding-left: 1px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: $color-zambezi;

  @include vp-1919 {
    max-width: 480px;
    margin-left: -1px;
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1439 {
    margin-bottom: 29px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-767 {
    margin-bottom: 14px;
  }
}

.main-header__link {
  width: max-content;
  display: flex;
  align-items: center;
  outline: none;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }

  @include vp-1919 {
    margin-left: 1px;
  }

  @include vp-1439 {
    margin-left: -1px;
  }
}

.main-header__icon {
  height: 27px;
  margin-left: 1px;
  margin-right: 10px;
  color: $color-alizarin-red;

  @include vp-1919 {
    margin-top: 1px;
  }

  @include vp-1439 {
    margin-top: 3px;
    margin-right: 14px;
    height: 23px;

    & svg {
      width: 23px;
      height: 23px;
    }
  }
}

.main-header__link span {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;

  @include vp-1439 {
    font-size: 14px;
    line-height: 18px;
  }
}
