.catalog-block {
  margin-bottom: 129px;
  border-bottom: $border-grey;
  border-top: $border-grey;
  overflow: hidden;

  @include vp-1919 {
    margin-bottom: 60px;
    border-top: none;
      border-bottom: none;
  }

  .title--h1 {
    padding-top: 26px;
    margin-left: -4px;

    @include vp-1919 {
      padding-top: 0;
      margin-bottom: 13px;
    }

    @include vp-1439 {
      margin-bottom: 15px;
    }
    @include vp-1279 {
      margin-bottom: 20px;
    }

    @include vp-767 {
      margin-bottom: 11px;
    }
  }

  &__tabs {
    display: flex;

    @include vp-1919 {
      flex-direction: column;
      border-top: none;
      border-bottom: none;
    }
  }

  &__tabs-controls {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 453px;
    width: 100%;
    padding-left: 64px;
    padding-top: 34px;
    padding-bottom: 34px;

    @include vp-1919 {
      max-width: 100%;
      flex-direction: row;
      align-items: flex-end;
      padding: 0 60px 25px;

      border: none;
    }

    @include vp-1279 {
      padding: 0 30px 31px;

    }

    @include vp-1023 {
      padding: 0 30px 15px;
    }

    @include vp-767 {
      flex-direction: column;
      align-items: flex-start;

      padding: 0 15px 15px;

    }
  }

  &__tabs-top {
    min-height: 410px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include vp-1919 {
      min-height: 10px;
    }
  }

  &__tabs-name {
    display: block;
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: $color-zambezi;

    @include vp-1919 {
      margin-bottom: 8px;
      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__tabs-btns {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include vp-1919 {
      flex-direction: row;
      flex-wrap: wrap;
    }

    @include vp-767 {
      margin-bottom: 15px;
    }
  }

  .btn {
    padding: 4px 15px;
    margin: 0 0 15px;
    color: $color-zambezi;
    border: 2px solid $color-zambezi;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    transition: color $trans-600;

    @include vp-1919 {
      margin-bottom: 0;
      padding: 4px 13px;
      font-size: 16px;
      margin-right: 15px;
      margin-bottom: 15px;
    }

    @include vp-1279 {
      padding: 1px 13px;
      font-size: 14px;
      margin-right: 15px;
    }

    &:last-child {
      margin-bottom: 0;

      @include vp-1919 {
        margin-right: 0;
      }
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color:$color-alizarin-red;
        background-color: $color-default-white;
        border-color: $color-alizarin-red;
      }
    }
  }

  .btn.is-active {
    color: $color-default-white;
    border: 2px solid $color-alizarin-red;
    background-color: $color-alizarin-red;
    transition: background-color $trans-600;

  }

  .link-download {
    min-width: 197px;

    @include vp-1279 {
      min-width: 177px;
    }
  }

  &__tabs-content {
    display: grid;
    grid-template-columns: repeat(4, 367px);
    padding: 0;
    margin: 0;
    list-style: none;
    border-left: $border-grey;


    // margin-top: 2px;



    @include vp-1919 {
      grid-template-columns: repeat(4, minmax(351px, 479px));

      padding: 0 15px;
      margin-top: 0;


      overflow: hidden;
      border-top: $border-grey;
      border-bottom: $border-grey;
      border-left: none;
    }

    @include vp-1439 {
      grid-template-columns: repeat(4, minmax(314px, 359px));
    }

    @include vp-1279 {
      grid-template-columns: repeat(3, minmax(329px, 420px));
    }

    @include vp-1023 {
      grid-template-columns: repeat(3, minmax(257px, 341px));
      padding: 0;
    }

    @include vp-767 {
      grid-template-columns: repeat(2, minmax(290px, 760px));
    }

    @include vp-639 {
      grid-template-columns: repeat(1, minmax(290px, 630px));
    }

    .catalog-block {
      &__item {
        height: 100%;

        border-right: $border-grey;
        border-bottom: $border-grey;

        overflow: hidden;

        margin-bottom: -2px;

        @include vp-1919 {
          max-width: 100%;
        }

        @include vp-1279 {
          margin-top: 2px;
        }

        @include vp-639 {
          border: none;
          border-bottom: $border-grey;
        }

        &:nth-child(2n) {
          @include vp-767 {
            border-right-color: transparent;
          }
        }

        &:nth-child(3n) {
          @include vp-1279 {
            border-right-color: transparent;
          }

          @include vp-767 {
            border-right-color: $color-whisper;
          }
        }

        &:nth-child(4n) {
          border-right-color: transparent;

          @include vp-1279 {
            border-right-color: $color-whisper;
          }
        }


        .card-block {
          display: flex;
          flex-direction: column;
          height: 100%;
          // padding: 22px;
        }

        .card-block__img img {
          transition: transform $trans-default;
        }

        .card-block__img {
          transition: opacity $trans-default;
        }

        .card-block__title {
          flex: 1 1 auto;
          display: flex;
          margin-bottom: 15px;
          color: $color-default-black;
          background-color: $color-transparent;
          text-transform: uppercase;
          word-break: break-word;

          font-weight: 700;
          font-size: 20px;
          line-height: 24px;

          @include vp-1919 {
            font-size: 16px;
            line-height: 20px;
          }

          @include vp-1279 {
            font-size: 14px;
            line-height: 17px;
          }
        }

        .link {
          margin-left: auto;

            @include vp-1023 {
            font-size: 10px;
            line-height: 20px;
          }

          @include vp-767 {
            line-height: 12px;

            svg {
              width: 22px;
              margin-left: 9px;
            }
          }
        }
      }
    }

    &--sectors {
      .catalog-block__item {
        min-height: 380px;

        @include vp-1919 {
          min-height: 350px;
        }

        @include vp-1439 {
          min-height: 330px;
        }

        @include vp-1279 {
          min-height: 340px;
        }

        @include vp-1023 {
          min-height: 270px;
        }

        @include vp-767 {
          min-height: 324px;

        }

        // @include vp-639 {
        //   padding: 0px;

        // }
      }

      .card-block {
        padding: 22px;

        @include vp-1919 {
          padding: 15px;
        }

        // @include vp-639 {
        //   padding: 0px;

        // }
      }

      .card-block__img {
        position: relative;
        margin-bottom: 15px;
        overflow: hidden;
        padding-bottom: percentage(220/320);

        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);

        @include vp-1919 {
          width: 100%;
          padding-bottom: percentage(230/322);
        }

        @include vp-1439 {
          padding-bottom: percentage(210/283);
        }

        @include vp-1279 {
          padding-bottom: percentage(224/301);
        }

        @include vp-1023 {
          padding-bottom: percentage(170/226);
        }

        @include vp-767 {
          padding-bottom: percentage(220/290);
        }

        img {
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &::before {
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          background: $black-to-right4;
          z-index: 1;
        }
      }
        @media (hover: hover), screen and (min-width: 0\0) {
        .card-block:hover .card-block__img img,
        .card-block:focus .card-block__img img {
          transform: scale(1.12);
        }

        .card-block:hover .card-block__img::before,
        .card-block:focus .card-block__img::before {
          opacity: 0;
        }

        .card-block:hover .link,
        .card-block:focus .link {
          color: $color-alizarin-red;
        }
      }
    }

    &--categories {
      .catalog-block__item {
        min-height: 762px;

        @include vp-1919 {
          min-height: 710px;
        }

        @include vp-1439 {
          min-height: 600px;
        }

        @include vp-1279 {
          min-height: 510px;
        }

        @include vp-1023 {
          min-height: 445px;
        }

        @include vp-767 {
          min-height: 476px;
        }

        @include vp-639 {
          min-height: 393px;
        }
      }

      .card-block {
        position: relative;
        width: 100%;
        max-width: 365px;
        padding: 21px 21px 21px 0;
        background-color: $color-transparent;
        cursor: pointer;
        transition: all $trans-default;

        @include vp-1919 {
          max-width: 100%;
        }

        @include vp-1023 {
          padding: 15px 15px 15px 0;
        }

        &__img {
          position: relative;
          margin-bottom: 15px;

          padding-bottom: percentage(574/320);
          transition: transform $trans-default;
          overflow: hidden;

          -webkit-backface-visibility: hidden;
          -moz-backface-visibility: hidden;
          -webkit-transform: translate3d(0, 0, 0);
          -moz-transform: translate3d(0, 0, 0);

          img {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;

            object-position: left;
          }

          @include vp-1919 {
            width: 100%;

          }

          @include vp-1439 {
            width: 100%;

            padding-bottom: percentage(502/320);

          }

          @include vp-1279 {
            padding-bottom: percentage(230/180);

          }

          @include vp-767 {
            padding-bottom: percentage(180/170);
          }

          @include vp-639 {
            padding-bottom: percentage(180/200);
            margin: 0 auto 20px;
          }
        }

        &__title {
          transition: color $trans-default;
          padding-left: 21px;
        }

        &__info {
          margin-bottom: 10px;
        }
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        .card-block:hover,
        .card-block:focus {
          background-color: $color-default-black;
        }

        .card-block:hover .card-block__title,
        .card-block:focus .card-block__title {
          color: $color-default-white;
        }

        .card-block:hover .link,
        .card-block:focus .link {
          color: $color-default-white;
        }

        .card-block:hover .card-block__img,

        .card-block:focus .card-block__img {
          transform: scale(1.13);

        }
      }
    }
  }
}


