.scroll-sequence__top {
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: column;

  @include vp-1023 {
    position: static;
    display: block;
  }
}

.scroll-sequence__slide {
  position: absolute;
  top: 100%;
  left: 0;

  width: 100%;

  @include vp-1023 {
    position: static;
  }
}

.scroll-sequence__slide-bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  width: 100%;
  height: 100%;

  background: $color-default-white;

  @include vp-1023 {
    display: none;
  }
}

.scroll-sequence__slide-content {
  position: relative;
  z-index: 2;
}

.scroll-sequence__bottom {
  position: relative;
}

.scroll-sequence__bottom-content {
  position: sticky;
  left: 0;
  top: 0;

  @include vp-1023 {
    position: static;
  }
}
