.greeting {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  margin-bottom: 101px;
  overflow: hidden;

  @include vp-1919 {
    margin-bottom: 70px;
  }

  @include vp-1439 {
    margin-bottom: 63px;
  }

  @include vp-1279 {
    margin-bottom: 60px;
  }

  @include vp-1023 {
    margin-bottom: 39px;
  }

  @include vp-767 {
    margin-bottom: 62px;
  }

  &__inner {
    display: grid;
    grid-template-columns: 960px 960px;

    @include vp-1919 {
      grid-template-columns: minmax(640px, 960px) minmax(737px, 960px);
      grid-gap: 62px;
    }

    @include vp-1439 {
      grid-template-columns: 640px minmax(547px, 737px);
      grid-gap: 102px;
    }

    @include vp-1279 {
      grid-template-columns: minmax(520px, 640px) minmax(437px, 547px);
      grid-gap: 75px;
    }

    @include vp-1023 {
      grid-template-columns: minmax(340px, 520px) minmax(385px, 437px);
      grid-gap: 50px;
    }

    @include vp-767 {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 0;
    }
  }

  &__img {
    max-width: 960px;

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  &__content {
    width: 100%;
    padding-top: 145px;
    padding-left: 147px;

    @include vp-1919 {
      margin-left: 8px;
      padding-top: 55px;
      padding-left: 10px;
    }

    @include vp-1439 {
      padding-top: 70px;
      padding-left: 0;
    }

    @include vp-1279 {
      padding-top: 74px;
    }

    @include vp-1023 {
      padding-top: 12px;
    }

    @include vp-767 {
      margin-left: 15px;
      padding-top: 30px;
    }

    .title--h2 {
      max-width: 386px;
      margin-top: 41px;
      margin-bottom: 53px;

      @include vp-1919 {
        max-width: 337px;
        margin-top: 19px;
        margin-bottom: 80px;
      }

      @include vp-1279 {
        margin-bottom: 37px;
      }

      @include vp-767 {
        margin-top: 13px;
        margin-bottom: 29px;
      }
    }
  }

  &__content-inner {
    // border-top: 2px solid $color-alizarin-red;
  }

  &__content-decor {
    display: block;
    width: 100%;
    height: 2px;
    background: $color-alizarin-red;
  }

  &__text-block {
    margin-left: -13px;

    @include vp-1919 {
      margin-left: -20px;
    }

    @include vp-1279 {
      margin-left: -16px;
    }

    @include vp-767 {
      margin-left: -5px;
      padding-right: 15px;
    }

    svg {
      width: 120px;
      height: 70px;

      @include vp-1919 {
        width: 103px;
        height: 60px;
      }

      @include vp-1279  {
        width: 69px;
        height: 40px;
      }

      @include vp-767 {
        width: 50px;
        height: 30px;
      }
    }
  }

  &__text {
    max-width: 530px;
    width: 100%;
    margin-top: -47px;
    padding-left: 100px;
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    color: $color-zambezi;

    @include vp-1919 {
      max-width: 450px;
      margin-top: -33px;
      padding-left: 73px;

      font-size: 18px;
      line-height: 24px;
    }

    @include vp-1279 {
      max-width: 390px;
      margin-top: -26px;
      padding-left: 50px;

      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      max-width: 100%;
      padding-right: 15px;
      padding-left: 20px;

      font-size: 14px;
      line-height: 18px;
    }
  }


  // [data-aos="new-transform"] {
  //   transform: translateX(100%);
  //   transition-property: transform, transform;

  //   &.aos-animate {
  //     transform: translateX(0);
  //   }
  // }
}
