.commodity {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @include vp-1439 {
    grid-template-columns: 1fr;
    display: flex;
    flex-wrap: wrap;
  }

  &__slider {
    order: 0;

    @include vp-1439 {
      flex-basis: 55%;
    }
  }

  &__characteristics {
    order: 1;

    @include vp-1439 {
      flex-basis: 45%;
    }
  }

  &__tabs {
    order: 2;

    @include vp-1439 {
      order: 3;
      flex-basis: 100%;
    }
  }

  &__wrap {
    order: 3;
    margin-top: -150px;

    @include vp-1439 {
      order: 2;
      margin-top: 0;
      display: flex;
    }
  }

  &__documents {
    margin: 0 0 30px;

    @include vp-1439 {
      margin: 0 30px 0 0;
    }
  }
}
