.main-content__filters {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  position: sticky;
  top: 14px;
  padding-top: 10px;
  z-index: 100;

  @include vp-1919 {
    padding-top: 0;
  }
}

.main-content--catalog .main-content__filters,
.main-content--rent .main-content__filters {
  padding-top: 50px;
  margin-top: -15px;

  @include vp-1919 {
    margin-top: 0;
    padding-top: 40px;
  }
}

.filters__toggle {
  display: none;

  @include vp-767 {
    display: block;
    margin-bottom: 4px;
    transition: all $trans-600;

    svg {
      color: $color-alizarin-red;
      cursor: pointer;
    }
  }
}

.filters__toggle-btn {
  @include vp-767 {
    margin: 0;
    padding: 2px 0;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    border-bottom: 2px dashed $color-alizarin-red;
    color: $color-default-black;
    transition: all $trans-600;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      color: $color-alizarin-red;
    }

    &:focus {
      color: $color-default-black;
    }
  }
}

.filters__wrapper {
  @include vp-767 {
    height: 0;
    overflow: hidden;
    transition: all $trans-600;
  }
}

.filters__toggle--active + .filters__wrapper--active {
  @include vp-767 {
    height: auto;
    overflow: visible;
  }
}

.filters__toggle--active svg {
  transform: rotateX(180deg);
  margin-top: 2px;
}

.filters--active {
  padding-top: 50px;

  @include vp-1919 {
    padding-top: 40px;
  }
}

.filters--active .filters__wrapper {
  position: relative;
}

.filters__catalog-section {
  display: flex;
  align-items: center;
  transition: all $trans-600;

  @include vp-767 {
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

.filters--active .filters__catalog-section {
  position: absolute;
  bottom: 0;
  left: -129px;
  height: 100%;

  @include vp-1439 {
    display: none;
  }
}

.catalog-section__title {
  margin: 1px 15px 0 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: $color-default-white;
  text-shadow: 0px 1px 0px $color-black-transparent;

  @include vp-1919 {
    margin-top: 2px;
    color: $color-default-black;
  }

  @include vp-1439 {
    margin-top: -1px;
    font-size: 14px;
    line-height: 23px;
  }

  @include vp-767 {
    width: 100%;
    margin-bottom: 10px;
    font-size: 12px;
    line-height: 20px;
  }
}

.filters--active .catalog-section__title {
  display: none;
}

.catalog-section__btn {
  margin: 0;
  padding: 8px 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  background-color: $color-black-transparent2;

  &:last-child {
    margin-left: 11px;
  }

  &--active {
    background-color: $color-alizarin-red;
    border-top: 3px solid $color-venetian-red;
    margin-top: -3px;
  }

  @include vp-1919 {
    &--active {
      border-top: 4px solid $color-venetian-red;
    }
  }

  @include vp-1439 {
    padding: 9px 15px 9px;
    font-size: 16px;
    line-height: 20px;

    &--active {
      border-top: 2px solid $color-venetian-red;
    }
  }

  @include vp-767 {
    padding: 8px 6px;

    &--active {
      margin-top: -2px;
    }
  }
}

.filters--active .catalog-section__btn:not(.catalog-section__btn--active) {
  display: none;
}

.filters--active .catalog-section__btn--active {
  height: 100%;
  display: flex;
  align-items: center;
  border: none;
}

.filters__section {
  transition: all $trans-600;
}

.filters__section .container {
  padding: 0;
}

.form-filters {
  position: relative;
  transition: all $trans-600;

  @include vp-1919 {
    margin-right: 30px;
  }

  @include vp-1439 {
    margin-right: 0;
  }
}

.filters--active .form-filters {
  &::before,
  &::after {
    position: absolute;
    bottom: 0;
    right: 0;
    content: "";
    width: 100vw;
    height: 100%;
    background-color: $color-zambezi;
    z-index: -1;
  }

  &::after {
    right: auto;
    left: 0;
  }
}

