.brands-item__buttons .custom-toggle {
  background-color: $color-default-black;
  color: $color-default-white;
  padding: 0;
  min-height: 27px;
  min-width: 102px;
  box-sizing: border-box;
  width: auto;
  align-items: center;
  justify-content: center;

  @include vp-767 {
    min-width: 90px;
    min-height: 25px;
  }
}

.brands-item__buttons label {
  padding: 0;
  margin: 0;
}

.brands-item__buttons .custom-toggle__icon {
  position: absolute;
  top: 29px;
  left: 20px;
  width: 12px;
  height: 12px;
  border: 1px solid $color-dark-gray;
  box-sizing: border-box;

  @include vp-767 {
    left: 10px;
  }

  &::before {
    position: absolute;
    top: -6px;
    left: 1px;
    content: url("../img/sprite/icon-check.svg");
    width: 11px;
    height: 14px;
    opacity: 0;
    transition: opacity $trans-default;
  }
}

.brands-item__buttons input:checked + .custom-toggle__icon {
  border: 1px solid $color-dark-gray;

  &::before {
    background-color: transparent;
    opacity: 1;
    transition: opacity $trans-default;
  }
}

.brands-item__buttons .custom-toggle__label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 27px;
  min-width: 102px;
  padding: 0 10px;

  @include vp-767 {
    min-width: 90px;
    min-height: 25px;
    padding: 0;
  }

  &::before {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    content: "";
    background-color: $color-default-black;
    z-index: 1;
    transition: opacity $trans-default;
  }
}

.brands-item__buttons input:checked {
  & ~ .custom-toggle__label::before {
    opacity: 1;
    transition: opacity $trans-default;
  }
}

.brands-item__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
}

.brands-item__link svg {
  margin-left: 5px;
}

.brands-sublist {
  position: absolute;
  top: 100%;
  left: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: transform $trans-default;
  transform: scaleY(0);
  transform-origin: 50% 0;

  &--active {
    transform: scaleY(1);
  }
}

.brands-sublist--active ~ .brands-list {
  transform: scaleY(0);
}

.brands-sublist .brands-item {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 490px;
