    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
    justify-content: flex-end;
    cursor: pointer;

    & svg {
      transform: rotate(180deg);
      width: auto;
      margin: 0;
      margin-right: 5px;
    }
  }

  &:last-child {
    border: 2px solid $color-default-black;
    min-height: 27px;
    padding: 2px 14px 2px 15px;

    & svg {
      display: none;
    }
  }
}

.brands-sublist .logo-wrapper {
