}

.form-range__item {
  display: flex;
  align-items: center;
  width: 50%;
  padding: 7px 18px 3px;
  border-bottom: 1px solid $color-whisper;

  &:first-child {
    border-right: 1px solid $color-whisper;
  }
}

.form-range input {
  border: none;
  margin-left: 5px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: $color-default-black;
  outline: none;
}

.js-range-slider {
  padding: 13px 19px 14px;
}

.form-range .noUi-horizontal {
  height: 2px;
  width: 83%;
  border: 0;
  margin-top: 5px;
  margin-bottom: 5px;
}

.form-range .noUi-base {
  background-color: $color-whisper;
  height: 2px;
  cursor: pointer;
}

.form-range .noUi-connect {
  height: 2px;
  background-color: $color-zambezi;
}

.form-range .noUi-handle {
  width: 10px;
  height: 10px;
  right: -4px;
  top: -4px;
  border: 0;
  border-radius: 50%;
  box-shadow: none;
  outline: none;
  background-color: $color-alizarin-red;
  cursor: pointer;

  &::before,
  &::after {
    display: none;
  }
}
