.form-main {
  position: relative;
  width: 100%;
  margin: 0 auto;
  min-height: 707px;

  background-color: $color-night-rider;

  @include vp-1919 {
    min-height: 676px;
  }

  @include vp-1279 {
    min-height: 554px;
  }

  @include vp-767 {
    min-height: 695px;
  }

  img {
    position: absolute;
    top: 0;

    width: 100%;
    height: 100%;
    margin: 0 auto;

    object-fit: cover;
    object-position: right;

    @include vp-767 {
      object-position: 90% 0;
    }
  }

  .feedback-form {
    margin: 0 auto;
    padding: 0;
    padding-top: 53px;
    color: $color-default-white;
    background-color: $color-night-rider;

    @include vp-1279 {
      padding-top: 30px;
    }

    &__container {
      max-width: 1398px;
      margin: 0 auto;
      padding-top: 41px;
      padding-right: 0;

      @include vp-1919 {
        padding-top: 37px;
        padding-left: 40px;
      }

      @include vp-1439 {
        padding-left: 60px;
      }

      @include vp-1279 {
        padding-top: 20px;
        padding-left: 30px;
        padding-right: 30px;
      }

      @include vp-767 {
        padding-left: 15px;
        padding-right: 15px;
      }

      &::before {
        display: none;
      }
    }

    &__title {
      @include vp-1919 {
        margin-bottom: 41px;
        font-size: 20px;
        line-height: 24px;
      }

      @include vp-1279 {
        margin-bottom: 20px;
        font-size: 16px;
        line-height: 20px;
      }

      @include vp-767 {
        margin-bottom: 15px;
      }
    }

    &__subtitle {
      @include vp-1919 {
        max-width: 440px;
        margin-bottom: 60px;
        font-size: 16px;
        line-height: 20px;
      }
      @include vp-1279 {
        max-width: 385px;
        margin-bottom: 39px;
        font-size: 14px;
        line-height: 18px;
      }

      @include vp-767 {

      }
    }

    &__form {
      @include vp-767 {
        max-width: 100%;
        grid-template-columns: repeat(1, 1fr);
        column-gap: 0;

        textarea {
          max-width: 100%;
          grid-column: 1/2;
        }
      }
    }

    .form__field {
      @include vp-767 {
        // grid-column: 1/2;
      }
    }

    .custom-toggle {

      @include vp-767 {
        max-width: 100%;

        &__label {
          max-width: 100%;
        }
      }
    }

    &__btn {
      margin-bottom: 20px;

      @include vp-1023 {
        min-width: 121px;
        padding: 12px 22px;
      }
    }
  }
}
