.link-download {
  width: max-content;
  display: flex;
  align-items: center;
  transition: color $trans-600;

  font-weight: 700;
  font-size: 16px;
  line-height: 20px;

  @include vp-1279 {
    font-size: 14px;
    line-height: 18px;
  }

  svg {
    margin-left: 1px;
    margin-right: 10px;
    color: $color-alizarin-red;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }
}
