.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 500;
  display: flex;
  min-height: 64px;
  border-bottom: 4px solid $color-alizarin-red;
  background-color: $color-default-black;

  @include vp-1919 {
    min-height: 54px;
  }
}

.header p,
.header a,
.header span {
  color: $color-default-white;
}

.header__inner {
  display: flex;
  align-items: center;
  align-self: stretch;
}

.header__logo {
  margin-right: 20px;
  margin-left: 20px;

  @include vp-1919 {
    margin-right: 30px;
    margin-left: 15px;
  }

  @include vp-1439 {
    margin-right: 28px;
  }

  @include vp-1279 {
    margin-right: auto;
  }
}

.header__logo img {
  @include vp-1919 {
    width: 140px;
    height: 17px;
  }

  @include vp-1439 {
    width: 120px;
    height: 15px;
  }
}

.header__nav-toggle {
  display: none;

  @include vp-1279 {
    align-self: stretch;
    display: block;
    width: 50px;
    padding: 0;
    border: none;
    outline: none;
    background-color: $color-default-white;
    cursor: pointer;
  }
}

.header__nav-toggle svg {
  color: $color-alizarin-red;
}

.header__nav {
  max-width: 875px;
  margin-left: auto;

  @include vp-1919 {
    max-width: 810px;
    margin-left: 0;
    margin-right: auto;
  }

  @include vp-1439 {
    max-width: 687px;
  }

  @include vp-1279 {
    max-width: none;
  }
}

.header__location,
.header__phone-box,
.header__log-in,
.header__search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: stretch;
  padding: 0 20px;
  border-left: 1px solid $color-night-rider;

  @include vp-1919 {
    padding: 0 15px;
  }

  @include vp-767 {
    display: none;
  }
}

.header__location a {
  line-height: 0;
  pointer-events: none;
  cursor: default;

  @include vp-1919 {
    pointer-events: auto;
    cursor: pointer;

    &:hover svg,
    &:focus svg {
      color: $color-default-white;
    }

    &:active svg {
      color: $color-default-white;
      opacity: 0.8;
    }
  }
}

.header__location svg {
  color: $color-alizarin-red;
  transition: color 0.6s ease;

  @include vp-1919 {
    width: 14px;
    height: 20px;
  }
}

.header__city-wrapper {
  margin-left: 10px;

  @include vp-1919 {
    display: none;
  }
}

.header__city-wrapper p {
  margin: 0;
  font-size: 12px;
  line-height: 14px;
  color: $color-aluminum;
}

.header__city-wrapper span {
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  text-decoration: underline;
  text-decoration-style: dashed;
  text-decoration-color: $color-alizarin-red;
  cursor: pointer;
  transition: color 0.6s ease;

  &:hover,
  &:focus {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}

.header__phone-box {
  @include vp-1919 {
    padding-right: 6px;
  }

  @include vp-1439 {
    padding: 0 12px;
  }

  @include vp-1279 {
    padding-right: 10px;
  }
}

.header__phone-box svg {
  fill: $color-alizarin-red;

  @include vp-1439 {
    width: 12px;
    height: 20px;
  }
}

.header__phone-number {
  margin-left: 10px;
}

.header__phone-number a {
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  transition: color 0.6s ease;

  @include vp-1919 {
    font-size: 14px;
  }

  &:hover,
  &:focus {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}

.header__phone-number p {
  margin: 0;
  font-size: 12px;
  line-height: 14px;
  color: $color-aluminum;

  @include vp-1919 {
    font-size: 10px;
  }
}

.header__log-in svg {
  margin-right: 12px;
  fill: $color-alizarin-red;

  @include vp-1919 {
    margin: 0;
  }
}

.header__log-in a,
.header__callback a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
}

.header__log-in span {
  transition: color 0.6s ease;

  @include vp-1919 {
    display: none;
  }
}

.header__log-in svg {
  transition: fill 0.6s ease;
}

.header__log-in a:hover span {
  color: $color-alizarin-red;
}

.header__log-in a:active span {
  color: $color-alizarin-red;
  opacity: 0.8;
}

.header__log-in a:hover svg {
  @include vp-1919 {
    fill: $color-default-white;
  }
}

.header__log-in a:active svg {
  @include vp-1919 {
    fill: $color-default-white;
    opacity: 0.8;
  }
}

.header__search-form {
  align-self: stretch;
  background-color: $color-default-white;
  border-left: 1px solid $color-night-rider;
}

.header__search-form form {
  display: flex;
}

.header__search-form svg {
  fill: $color-alizarin-red;

  @include vp-1919 {
    width: 20px;
    height: 20px;
  }
}

.header__search-form input {
  max-width: 58px;
  margin-left: 5px;
  border: none;
  outline: none;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;

  @include vp-1919 {
    display: none;
  }
}

.header__search-form input::placeholder {
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  color: $color-zambezi;
}

.header__callback {
  display: flex;
  align-self: stretch;
  transition: opacity 0.6s ease;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.7;
  }
}

.header__callback a {
  align-items: center;
  align-self: stretch;
  padding-left: 20px;
  padding-right: 20px;
  background-color: $color-alizarin-red;
  color: $color-default-white;

  @include vp-1919 {
    padding: 0 15px;
  }
}

.header__callback svg {
  margin-right: 8px;
  fill: $color-default-white;
}

.header__callback span {
  @include vp-1439 {
    font-size: 10px;
    line-height: 12px;
  }
}
