.product-info {
  max-width: 428px;
  height: 437px;
  padding: 30px 20px;

  display: flex;
  flex-direction: column;
  row-gap: 30px;

  border: 2px solid $color-default-black;

  @include vp-1439 {
    max-width: 396px;
    padding: 20px 10px 15px;
  }

  @include vp-767 {
    width: 100%;
    padding: 15px 0;
  }

  &__btns {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 15px;

    & .btn-reservation {
      margin: 0;
      min-width: 230px;
      padding: 15px 0;

      font-size: 18px;
      line-height: 16px;
      text-align: center;

      @include vp-767 {
        min-width: 180px;

        font-size: 14px;
      }

      &:disabled {
        opacity: 0.5;

        pointer-events: none;
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          opacity: 0.8;
        }
      }
    }
  }

  &__button-compare {
    min-width: 205px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: 600px;
    font-size: 14px;
    line-height: 17px;

    background-color: transparent;
    border: none;
    cursor: pointer;

    @include vp-767 {
      min-width: 180px;

      font-size: 12px;
      line-height: 14px;
    }

    &:disabled {
      opacity: 0.5;

      pointer-events: none;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
      }
    }

    svg {
      margin-right: 10px;
    }
  }
}
