.main-content__sidebar {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  max-width: 275px;
  width: 275px;
  height: max-content;
  margin: 50px 0 0 40px;
  box-sizing: border-box;

  @include vp-1919 {
    margin: 41px 0 0 30px;
  }

  @include vp-1439 {
    margin: 41px auto 0;
  }

  @include vp-1279 {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }

  @include vp-767 {
    margin-top: 43px;
  }
}

.sidebar__btn {
  display: none;

  @include vp-1279 {
    width: fit-content;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    border: 1px solid $color-dark-gray;
    box-sizing: border-box;
    color: $color-default-black;
    // background-color: $color-default-white;
    margin: 0 0 17px;
    padding: 9px 14px;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
  }

  @include vp-767 {
    padding: 3px 9px;
    font-size: 16px;
    margin-bottom: 10px;
  }

  &--active {
    background-color: $color-whisper;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      opacity: 1;
      background-color: $color-whisper;
    }
  }
}

.sidebar__btn svg {
  @include vp-1279 {
    margin-right: 16px;
  }
}

.sidebar__container {
  background-color: $color-default-white;
  border: 1px solid $color-whisper;

  @include vp-1279 {
    display: flex;
    height: 0;
    overflow: hidden;
    transition: all $trans-600;
    border: none;
  }

  @include vp-767 {
    flex-direction: column;
  }
}


.sidebar__btn--active + .sidebar__container {

  // max-height: 1000px;
  height: auto;
  margin: 19px 0;
  border: 1px solid $color-whisper;

  @include vp-1023 {
    margin-bottom: 24px;
  }

  @include vp-767 {
    border-top: none;
  }
}

.sidebar__wrapper {
  @include vp-1279 {
    width: 50%;
    border-right: 1px solid $color-whisper;

    &:last-child {
      border-right: none;
    }
  }

  @include vp-767 {
    width: 100%;
    border-right: none;
  }
}

.main-content--catalog .main-content__sidebar,
.main-content--rent .main-content__sidebar,
.main-content--commodity .main-content__sidebar {
  margin-top: 0;

  @include vp-1279 {
    width: 50%;
    margin-left: 0;

    .sidebar__wrapper {
      width: 100%;
    }
  }

  @include vp-767 {
    width: 100%;
  }
}

.main-content--catalog .main-content__sidebar,
.main-content--rent .main-content__sidebar {
  padding-top: 36px;

  @include vp-1919 {
    padding-top: 40px;
  }
}


.sidebar__list,
.sidebar__sublist {
  margin: 0;
  padding: 0;
  list-style: none;
  transition: all $trans-600;
}

.sidebar__list {
  border-top: 1px solid $color-whisper;
}

.sidebar__wrapper:first-child .sidebar__list:first-child {
  border-top: none;

  @include vp-767 {
    border-top: 1px solid $color-whisper;
  }
}

.sidebar__list:first-child {
  @include vp-1439 {
    border-top: none;
  }

  @include vp-767 {
    border-top: 1px solid $color-whisper;
  }
}

.sidebar__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 17px 0 15px;

  &:first-child {
    cursor: pointer;
    padding-top: 13px;
    padding-bottom: 11px;
  }

  @include vp-1919 {
    padding-left: 15px;
    padding-right: 16px;

    &:first-child {
      padding-top: 14px;
      padding-bottom: 10px;
    }

    &:last-child {
      padding-left: 18px;
    }
  }

  @include vp-1023 {
    padding-left: 14px;
    padding-right: 18px;

    &:first-child {
      padding-top: 12px;
      padding-bottom: 12px;
    }

    &:last-child {
      padding-left: 17px;
    }
  }
}

.main-content--catalog .sidebar__item,
.main-content--rent .sidebar__item {
  padding-left: 19px;

  @include vp-1919 {
    padding-left: 15px;
  }
}

.sidebar__list:first-child .sidebar__item {
  padding-left: 18px;
  padding-right: 16px;

  @include vp-1023 {
    padding-left: 17px;
  }
}

.sidebar__list--active .sidebar__item {
  &:first-child {
    padding-top: 13px;
    padding-bottom: 10px;
  }

  @include vp-1919 {
    &:first-child {
      padding-top: 14px;
      padding-bottom: 11px;
    }

    &:last-child {
      padding-left: 18px;
    }
  }

  @include vp-1023 {
    &:first-child {
      padding-top: 12px;
      padding-bottom: 11px;
    }

    &:last-child {
      padding-left: 17px;
    }
  }
}

.sidebar__title {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.15px;
  margin: 0;
  padding: 0;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      font-weight: 700;
    }
  }

  @include vp-767 {
    font-size: 14px;
  }
}

.sidebar__list:first-child .sidebar__title {
  @include vp-1919 {
    &:first-child {
      letter-spacing: 0;
    }
  }
}

.sidebar__icon {
  transition: transform $trans-600;
  margin-top: -5px;
  color: $color-dark-gray;
  display: flex;

  & svg {
    width: 11px;
    height: 6px;
  }

  @include vp-1023 {
    margin-top: -1px;

    & svg {
      width: 12px;
      height: 7px;
    }
  }
}
.sidebar__list--active .sidebar__title {
  font-weight: 700;
}

.sidebar__list--active .sidebar__icon {
  transform: rotateX(180deg);
  margin-top: 2px;
}

.sidebar__list--active .sidebar__item:first-child {
  background-color: $color-whisper;
  border-bottom: 1px solid $color-whisper;
}

.sidebar__sublist {
  max-height: 0;
  overflow: hidden;
  transition: all $trans-600;
}

.sidebar__list--active .sidebar__sublist {
  padding: 16px 0 30px;
  max-height: 1000px;

  @include vp-1919 {
    padding: 17px 0 28px;
  }

  @include vp-1023 {
    padding-bottom: 29px;
  }
}

.main-content--catalog .sidebar__list--active,
.main-content--rent .sidebar__list--active {
  @include vp-1919 {
    & .sidebar__sublist {
      padding: 16px 0 12px;
    }
  }
}

.sidebar__sublist .sublist-item:not(:first-child) {
  margin-top: 10px;
}

.sidebar__sublist .sublist-item__link {
  font-size: 16px;
  line-height: 20px;

  &--active {
    font-weight: 700;
    text-decoration: none;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-black;
      }
    }
  }

  @include vp-1023 {
    max-width: 216px;
  }

  @include vp-767 {
    max-width: 100%;
    font-size: 14px;
    line-height: 18px;
  }
}

.sidebar__sublist .sublist-item__icon {
  margin-top: 5px;

  @include vp-1919 {
    margin-top: 3px;
  }
}
