.content-catalog__wrapper {
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 80px;
  max-width: 1920px;
  margin: 0 auto;

  @include vp-1919 {
    column-gap: 60px;
  }

  @include vp-1279 {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
}

.content-block {
  padding: 101px 0 0 60px;

  @include vp-1919 {
    padding-top: 61px;
  }

  @include vp-1279 {
    padding: 30px;
  }

  @include vp-767 {
    padding: 30px 15px 30px;
  }
}

.content-block__title {
  max-width: 820px;
  margin-bottom: 20px;
  font-size: 32px;
  line-height: 45px;
  text-transform: none;

  @include vp-1919 {
    max-width: 600px;
    font-size: 32px;
    line-height: 45px;
  }

  @include vp-1439 {
    max-width: 637px;
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 16px;
  }

  @include vp-1279 {
    font-size: 24px;
    line-height: 32px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 15px;
  }
}

.content-block__description {
  max-width: 800px;
  line-height: 20px;
  margin: 0 0 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @include vp-1919 {
    max-width: 600px;
    margin-bottom: 15px;
  }

  @include vp-1439 {
    margin-bottom: 15px;
    max-width: 637px;
  }

  @include vp-767 {
    max-width: 100%;
    font-size: 14px;
    line-height: 18px;
  }
}
