.aurora-main {
  position: relative;
  width: 100%;
  margin: 0 auto;
  min-height: 900px;
  overflow: hidden;

  @include vp-1919 {
    min-height: 650px;
  }

  @include vp-1279 {
    min-height: 520px;
  }

  @include vp-639 {
    min-height: 666px;
  }

  color: $color-default-white;
  background: $color-alizarin-red;
  z-index: 1;

  &__img {
    position: absolute;
    margin: 0 auto;
    width: 1298px;
    left: -140px;
    right: 0;

    @include vp-1919 {
      width: 938px;
      left: -11px;
    }

    @include vp-1439 {
      left: -114px;
    }

    @include vp-1279 {
      width: 750px;
      left: 0;
    }

    @include vp-1023 {
      left: -70px;
    }

    @include vp-639 {
      width: 404px;
      height: 280px;
      bottom: 0;
      left: 0;
    }

    img {
      width: 100%;
      object-fit: contain;
    }
  }

  .title--h1 {
    padding-top: 60px;
    padding-left: 60px;

    @include vp-1279 {
      padding-top: 32px;
      padding-left: 30px;
    }

    @include vp-767 {
      padding-right: 15px;
    }

    @include vp-639 {
      padding-top: 30px;
      padding-left: 15px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2,960px);
    grid-gap: 10px 25px;
    list-style: none;

    @include vp-1919 {
      grid-template-columns: repeat(2, minmax(720px, 960px));
    }

    @include vp-1439 {
      grid-template-columns: repeat(2, minmax(640px, 720px));
    }

    @include vp-1279 {
      grid-template-columns: repeat(2, minmax(512px, 640px));
    }

    @include vp-1023 {
      grid-template-columns: repeat(2, minmax(384px, 512px));
    }

    @include vp-767 {
      grid-template-columns: repeat(2, minmax(320px, 384px));
    }

    @include vp-639 {
      grid-auto-flow: row;
      grid-template-columns: 1fr;
      grid-gap: 5px;
    }
  }

  &__item {
    @include vp-639 {
      margin-left: 0;
      margin-bottom: 25px;
    }
  }

  &__item--text {
    margin-bottom: 49px;

    @include vp-1919 {
      margin-bottom: 7px;
    }
  }


  &__number {
    font-family: $ff-ttautonomous;
    font-weight: 800;
  }

  &__item--big,
  &__item--middle-right,
  &__item--small-right {
    position: relative;
    padding-right: 36%;
    margin-left: auto;

    @include vp-1919 {
      padding-right: 37%;
    }

    @include vp-1439 {
      padding-right: 23%;
    }

    @include vp-639 {
      margin-top: 0;
      padding-left: 15px;
      padding-right: 15px;
      // padding-top: 2px;
    }
  }

  &__item--big {
    // grid-column-start: 1;
    // grid-column-end: 3;
    // margin-bottom: 180px;
    padding-top: 7px;

    @include vp-1919 {
      padding-top: 0;
      margin-top: -33px;
    }

    @include vp-1279 {
      padding-top: 54px;
      // margin-bottom: 142px;
      padding-right: 31%;
    }

    @include vp-1023 {
      padding-top: 74px;
    }

    @include vp-639 {
      padding-top: 0;
    }

    .aurora-main__number {
      font-size: 100px;
      line-height: 140px;

      @include vp-1919 {
        font-size: 70px;
        line-height: 98px;
      }

      @include vp-1279 {
        font-size: 50px;
        line-height: 70px;
      }

      @include vp-639 {
        font-size: 30px;
        line-height: 42px;
      }
    }

    .aurora-main__number-text {
      margin-top: -20px;

      @include vp-1919 {
        margin-top: -10px;
      }

      @include vp-1279 {
        margin-top: -5px;
      }

      @include vp-639 {
        margin-top: 0;
      }

    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      height: 2px;
      width: 100%;
      background-color: $color-default-white;

      @include vp-1279 {
        top: 52px;
      }

      @include vp-1023 {
        top: 74px;
      }

      @include vp-639 {
        width: 150px;
        left: 0;
        top: 0;
      }
    }
  }

  &__item--middle-left {
    position: relative;
    padding-left: 33%;
    // margin-top: -122px;
    // margin-bottom: 106px;
    padding-top: 18px;

    @include vp-1919 {
      // margin-top: -151px;
      padding-top: 2px;
      padding-left: 30%;
    }

    @include vp-1439 {
      padding-left: 16%;
    }

    @include vp-1279 {
      // margin-top: -141px;
      padding-left: 19%;
    }

    @include vp-1023 {
      padding-top: 1px;
    }

    @include vp-639 {
      // margin-top: 0;
      padding-top: 0;
      padding-left: 15px;
      // margin-bottom: 0;
    }

    .aurora-main__number-text {
      margin-top: -12px;

      @include vp-1919 {
        margin-top: -6px;
      }

      @include vp-639 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 2px;
      width: 59%;
      background-color: $color-default-white;

      @include vp-1919 {
        width: 56%;
      }

      @include vp-1439 {
        width: 44%;
      }

      @include vp-1279 {
        width: 47%;
      }

      @include vp-1023 {
        width: 55%;
      }

      @include vp-639 {
        width: 123px;
      }
    }
  }

  &__item--middle-right {
    padding-top: 126px;
    margin-bottom: 20px;

    @include vp-1919 {
      padding-top: 92px;
      padding-right: 27%;
    }

    @include vp-1439 {
      padding-right: 12%;
    }

    @include vp-1279 {
      padding-right: 17%;
      padding-top: 71px;
    }

    @include vp-1023 {
      padding-right: 15%;
    }

    @include vp-639 {
      padding-top: 0;
    }

    .aurora-main__number-text {
      margin-top: -12px;

      @include vp-1919 {
        margin-top: -6px;
      }

      @include vp-639 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 107px;
      right: 0;
      height: 2px;
      width: 100%;
      background-color: $color-default-white;

      @include vp-1919 {
        top: 88px;
      }

      @include vp-1279 {
        top: 69px;
      }

      @include vp-639 {
        width: 97px;
        left: 0;
        top: 0;
      }
    }
  }

  &__item--small-right {
    // padding-top: 23px;
    padding-top: 145px;
    // margin-bottom: 107px;

    @include vp-1919 {
      // margin-top: -67px;
      // margin-bottom: 90px;
      padding-top: 97px;
      padding-right: 29%;
    }

    @include vp-1439 {
      padding-right: 14%;
    }

    @include vp-1279 {
      padding-right: 15%;
      padding-top: 78px;
      // margin-bottom: 0;
    }

    @include vp-1023 {
      // margin-top: -63px;
      // padding-top: 8px;
    }

    @include vp-639 {
      padding-top: 0;
      // margin-bottom: 0;
    }

    .aurora-main__number-text {
      margin-top: -5px;

      @include vp-1919 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 121px;
      right: 0;
      height: 2px;
      width: 100%;
      background-color: $color-default-white;

      @include vp-1919 {
        top: 85px;
      }

      @include vp-1279 {
        top: 76px;
      }

      @include vp-639 {
        width: 135px;
        left: 0;
        top: 0;
      }
    }
  }

  &__item--small-left {
    position: relative;
    padding-top: 15px;
    padding-left: 33%;
    // margin-top: -120px;

    @include vp-1919 {
      padding-left: 24%;
      padding-top: 11px;
      // margin-top: -86px;
    }

    @include vp-1439 {
      padding-left: 8%;
    }

    @include vp-1279 {
      // margin-top: -72px;
      padding-top: 3px;
      padding-left: 10%;
    }

    @include vp-1023 {
      padding-left: 8%;
      padding-top: 2px;
    }

    @include vp-639 {
      // margin-top: 0px;
      padding-top: 0;
      padding-left: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      top: -10px;
      left: 0;
      height: 2px;
      width: 48.5%;
      background-color: $color-default-white;

      @include vp-1919 {
        width: 42%;
        top: 2px;
      }

      @include vp-1439 {
        width: 28%;
      }

      @include vp-1279 {
        top: -1px;
      }

      @include vp-1023 {
        width: 32%;
      }

      @include vp-639 {
        width: 110px;
      }
    }
  }



  &__item--middle-right,
  &__item--middle-left {
    .aurora-main__number {
      font-size: 70px;
      line-height: 98px;

      @include vp-1919 {
        font-size: 50px;
        line-height: 70px;
      }

      @include vp-1279 {
        font-size: 40px;
        line-height: 56px;
      }

      @include vp-639 {
        font-size: 30px;
        line-height: 42px;
      }
    }
  }

  &__item--small-left,
  &__item--small-right {
    .aurora-main__number {

      font-size: 50px;
      line-height: 70px;

      @include vp-1919 {
        font-size: 40px;
        line-height: 56px;
      }

      @include vp-1279 {
        font-size: 30px;
        line-height: 42px;
      }
    }
  }

  &__text {
    max-width: 460px;
    margin: 30px 0 10px;
    padding-left: 60px;

    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    @include vp-1919 {
      margin-top: 6px;
      // margin-bottom: 28px;
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1279 {
      margin-top: 21px;
      padding-left: 32px;
      font-size: 14px;
      line-height: 17px;
    }

    @include vp-639 {
      margin-top: 11px;
      margin-bottom: 0;
      padding-left: 15px;
      padding-right: 15px;
      font-size: 14px;
      line-height: 17px;
    }
  }

  &__number-text {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    @include vp-1919 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1279 {
      font-size: 14px;
      line-height: 17px;
    }
  }
}

