.main-content {
  &--construction,
  &--catalog,
  &--rent {
    overflow-x: clip;
    padding-bottom: 100px;
    border-bottom: 1px solid $color-whisper;
  }

  @include vp-1919 {
    &--construction,
    &--catalog,
    &--rent {
      padding-bottom: 60px;
    }
  }

  @include vp-767 {
    &--construction,
    &--catalog,
    &--rent {
      padding-bottom: 30px;
    }
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-black-transparent2;
  z-index: 10;
  transition: transform $trans-default;
  transform: scaleY(0);
  transform-origin: 50% 0;

  &--active {
    transform: scaleY(1);
  }
}

.main-content__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 30px;
  padding-top: 3px;

  @include vp-1439 {
    padding: 0 15px;
  }

  @include vp-1279 {
    display: flex;
    flex-direction: column;
    gap: 0;
    padding: 0 30px;
  }

  @include vp-1023 {
    // padding: 0 30px;
  }
}
