.tabs__controls {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-767 {
    justify-content: space-between;
  }
}

.tabs__control {
  position: relative;
  margin-right: 10px;
  padding: 0 20px;

  font-weight: 700;
  font-size: 16px;
  line-height: 34px;

  border: none;
  color: $color-default-white;
  background-color: $color-black-transparent3;
  transition: background-color 0.2s ease;

  @include vp-767 {
    padding: 5px 0;
    font-size: 12px;
    flex-grow: 1;

    &:last-child {
      margin-right: 0;
    }
  }

  &.is-active {
    background-color: $color-whisper;
    color: $color-zambezi;
  }

  &:hover {
    cursor: pointer;
  }
}

.tabs__element {
  width: 100%;
  max-width: 733px;
  padding: 30px 20px;

  border: 1px solid $color-whisper;

  @include vp-1279 {
    padding: 15px 10px;
  }

  @include vp-767 {
    max-width: 100%;
    padding: 15px 5px;
  }

  &--description-heading {
    margin-bottom: 10px;
    color: $color-alizarin-red;

    @include vp-1279 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-1023 {
      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 22px;
    }
  }

  &--video {
    iframe {
      display: block;
      margin: 0 auto;
      width: 600px;
      height: 400px;

      @include vp-1439 {
        width: 500px;
        height: 300px;
      }

      @include vp-1023 {
        width: 400px;
        height: 300px;
      }

      @media (max-width: 500px) {
        width: 266px;
        height: 190px;
      }
    }
  }
}
