.product-slider {
  position: relative;

  max-width: 733px;
  height: 590px;
  padding: 20px;
  outline: 2px solid blue;

  @include vp-1439 {
    max-width: 589px;
  }

  // @include vp-1023 {
  //   width: 403px;
  // }

  // @include vp-767 {
  //   width: 100%;
  //   min-width: 290px;
  // }
}

.product-slider__main-wrapper {
  position: relative;
  overflow-x: hidden;
}

.product-slider__label {
  position: absolute;
  z-index: 9;
  top: 0;
  right: -10px;
}

.product-slider__main {
  width: 100%;
  margin-bottom: 34px;
  height: 400px;

  // @include vp-1279 {
  //   height: 320px;
  //   margin-bottom: 14px;
  // }

  // @include vp-1023 {
  //   height: 320px;
  //   margin-bottom: 10px;
  // }

  // @include vp-767 {
  //   height: auto;
  //   min-height: 240px;
  // }
}

.product-slider__thumbs-wrapper {
  position: relative;
}

.product-slider__main-list,
.product-slider__thumbs-list,
.product-slider__modal-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.product-slider__main-item {
  width: 693px;
  height: 400px;

  // @include vp-1279 {
  //   width: 385px;
  //   height: 320px;
  // }

  // @include vp-1023 {
  //   width: 403px;
  //   height: 320px;
  // }

  // @include vp-767 {
  //   width: 100%;
  //   height: auto;
  //   min-width: 290px;
  //   min-height: 240px;
  // }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &:hover {
    cursor: pointer;
  }
}

.product-slider__thumbs-item {
  position: relative;

  min-width: 120px;
  max-width: 120px;
  min-height: 110px;
  max-height: 110px;

  border: 1px solid $color-dark-gray;

  // transition: opacity $trans--300;

  // @include vp-767 {
  //   width: auto;
  //   height: auto;
  //   min-width: 60px;
  //   min-height: 60px;
  // }

  @include vp-1023 {
    min-width: 99px;
    max-width: 99px;
    min-height: 90px;
    max-height: 90px;
  }

  @media (max-width: 320px) {
    min-width: 75px;
    max-width: 75px;
    min-height: 68px;
    max-height: 68px;
  }

  // &::before {
  //   // transition: opacity $trans--300;
  //   content: "";
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   width: 100%;
  //   height: 100%;
  //   background-color: rgba($color-conifer, 0.2);
  //   opacity: 0;
  // }

  &--active {
    border: 3px solid $color-alizarin-red;

    // &::before {
    //   content: "";
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   width: 100%;
    //   height: 100%;
    //   background-color: rgba($color-conifer, 0.2);
    //   opacity: 1;
    // }
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &:hover {
    opacity: 0.8;
    cursor: pointer;

    &::before {
      opacity: 1;
    }
  }
}

.product-slider__button,
.product-slider__button-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1;
  bottom: 20px;
  border: 0;
  margin: 0;
  padding: 0;
  top: 50%;
  color: $color-default-white;
  transform: translateY(-50%);
  background-color: transparent;
  // transition: opacity $trans--300;

  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }

  &--disabled {
    opacity: 0.6;

    &:hover {
      cursor: default;
      opacity: 0.6;
    }
  }
}

.product-slider__button-modal {
  top: 48%;
}

.product-slider__button {
  width: 30px;
  height: 28px;

  svg {
    width: 30px;
    height: 28px;
  }

  &--next {
    right: -15px;
  }

  &--prev {
    left: -15px;
  }
}

.product-slider__button-modal {
  width: 64px;
  height: 60px;

  svg {
    width: 64px;
    height: 60px;
  }

  &--next {
    right: -35px;
  }

  &--prev {
    left: -35px;
  }
}

// Modal

.product-slider__modal-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100vw;
  height: 100vh;
  background-color: rgba($color-default-black, 0.4);

  &--active {
    display: block;
  }
}

.product-slider__modal-wrapper {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  max-width: calc(970 / 1920 * 100%);
  height: auto;

  // @media (max-width: 1700px) {
  //   max-width: 60%;
  // }

  // @include vp-1439 {
  //   max-width: 70%;
  // }

  // @include vp-1279 {
  //   max-width: 80%;
  // }

  // @include vp-1023 {
  //   max-width: 90%;
  // }

  // img {
  //   width: 100%;
  //   height: 100%;
  // }
}

.product-slider__modal-button-close {
  position: absolute;
  z-index: 4;
  top: 15px;
  right: 15px;
}
