.main-header--construction {
  position: relative;
  border: none;

  @include vp-767 {
    min-height: 172px;
  }
}

.main-header__image {
  position: absolute;
  top: -2px;
  left: 0;
  right: 0;
  width: 100%;
  height: 275px;
  margin: 0 auto;
  z-index: 0;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    background-image: $black-to-right3;
    z-index: 1;
  }

  @include vp-1919 {
    height: 205px;
    top: 0;
  }

  @include vp-1439 {
    height: 178px;
  }

  @include vp-1023 {
    height: 174px;
  }

  @include vp-767 {
    height: 172px;
  }
}

.main-header__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include vp-1439 {
    object-fit: fill;
  }
}

.main-header--construction .container {
  position: relative;
  padding: 28px 60px 20px;

  @include vp-1919 {
    padding-bottom: 56px;
  }

  @include vp-1439 {
    padding-bottom: 75px;
  }

  @include vp-1023 {
    padding: 26px 29px 79px;
  }
}

.main-header--construction .breadcrumbs__item {
  &::after {
    background-color: $color-default-white;
    text-shadow: 0px 1px 0px $color-black-transparent;
  }
}

.main-header--construction .breadcrumbs__link {
  color: $color-default-white;
  text-shadow: 0px 1px 0px $color-black-transparent;

  &::before {
    background-color: $color-default-white;
    text-shadow: 0px 1px 0px $color-black-transparent;
  }
}

.main-header--construction .main-header__title {
  color: $color-default-white;
  text-shadow: 2px 2px 0px $color-black-transparent;
}
