.product-specs {
  &__item {
    margin: 0;
    padding: 5px 10px;

    display: grid;
    grid-template-columns: 28px 1fr 1fr;
    gap: 0 10px;
    align-items: center;

    &:nth-child(even) {
      background-color: $color-white-smoke;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 24px;
    color: $color-zambezi;

    @include vp-1279 {
      font-size: 12px;
      line-height: 20px;
    }

    &--value {
      margin-left: auto;
      font-weight: 700;
    }
  }
}
