.project-geography {
  position: relative;
  min-height: 1093px;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  color: $color-default-white;
  background: linear-gradient(266.2deg, rgba(0, 0, 0, 0.8) 3.38%, #000000 96.89%);

  @include vp-1919 {
    min-height: 884px;
    margin-bottom: 63px;
  }

  @include vp-1439 {
    min-height: 849px;
  }

  @include vp-1279 {
    min-height: 0;
    background: none;
    margin-bottom: 57px;
  }

  @include vp-1023 {
    margin-bottom: 53px;

  }

  @include vp-767 {
    // min-height: 0;
  }

  @include vp-639 {
    min-height: 478px;
    margin-bottom: 50px;
  }

  &__wrapper {
    @include vp-1279 {
      background: linear-gradient(266.2deg, rgba(0, 0, 0, 0.8) 3.38%, #000000 96.89%);
      min-height: 800px;

      @include vp-1023 {
        min-height: 700px;
      }

      @include vp-767 {
        min-height: 480px;
      }

      @include vp-639 {
        display: flex;
        flex-direction: column;
      }
    }
  }

  &__wrapper-slider {
    @include vp-1439 {
      display: flex;
      flex-direction: column;
    }

    @include vp-1279 {
      margin-top: -257px;
    }

    @include vp-1023 {
      margin-top: -244px;
    }

    @include vp-767 {
      margin-top: -110px;
    }
  }

  .container {
    @include vp-1439 {
      // display: flex;
      // flex-direction: column;
    }
  }

  &__img {
    position: relative;
    max-width: 1257px;
    width: 100%;
    margin-left: 134px;

    @include vp-1919 {
      max-width: 1000px;
      margin-left: 41px;
    }

    @include vp-1439 {
      max-width: 900px;
      margin-left: 30px;
    }

    @include vp-1279 {
      margin-left: 54px;
    }

    @include vp-1023 {
      max-width: 707px;
      margin-left: 29px;
    }

    @include vp-767 {
      max-width: 760px;
      margin-left: 0;
    }

    @include vp-639 {
      order: 2;
    }

    img {
      position: absolute;
      margin-top: -112px;
      width: 100%;

      object-fit: contain;

      @include vp-1279 {
        margin-top: 10px;
      }

      @include vp-1023 {
        margin-top: 30px;
      }

      @include vp-639 {
        position: relative;
        margin-top: -17px;
      }
    }
  }

  &__text-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 75px;
    margin-left: auto;
    margin-right: 60px;

    @include vp-1919 {
      margin-top: 55px;
    }

    @include vp-1439 {
      margin-bottom: 137px;
    }

    @include vp-1279 {
      align-items: flex-start;
      margin-left: 0;
      margin-top: 32px;
      padding-left: 30px;
    }

    @include vp-767 {
      padding-left: 15px;
      margin-right: 0;
      padding-right: 15px;
    }

    @include vp-639 {
      margin-bottom: 0;
    }

    .title--h1 {
      margin-bottom: 30px;
      text-align: right;

      @include vp-1919 {
        margin-bottom: 10px;
      }

      @include vp-1279 {
        margin-bottom: 21px;
        text-align: left;
      }

      @include vp-767 {
        margin-bottom: 9px;
      }
    }
  }

  &__text {
    width: 100%;
    max-width: 390px;
    margin: 0;
    margin-bottom: 20px;
    text-align: right;

    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    @include vp-1919 {
      max-width: 375px;
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1279 {
      max-width: 328px;
      text-align: left;
      font-size: 14px;
      line-height: 17px;
    }

    @include vp-767 {
      max-width: 100%;
    }
  }

  &__slider {
    position: relative;
    display: flex;
    margin-left: 20px;
    margin-top: 316px;
    overflow: hidden;

    @include vp-1919 {
      max-width: 73%;
      margin-left: 15px;
      margin-top: 209px;
    }

    @include vp-1439 {
      max-width: 82%;
      order: 2;
      margin-top: 0;
    }

    @include vp-1279 {
      max-width: 100%;
      margin-right: 15px;
      margin-top: 0;
    }

    @include vp-1023 {
      margin-right: 0;


      // margin-left: 30px;
    }

    @include vp-767 {
      margin-left: 7.5px;

      padding-bottom: 55px;
    }
  }

  &__cards {
    position: relative;
    z-index: 1;
    display: flex;

    flex-wrap: wrap;
    margin: 0 0 59px;

    padding: 0;

    list-style: none;

    @include vp-1279 {
      margin-bottom: 0;
    }

    @include vp-1023 {
      flex-wrap: nowrap;
    }

    @include vp-639 {
      // overflow: hidden;
      // width: 100%;

    }
  }

  .card {
    position: relative;
    width: 100%;
    z-index: 2;
    max-width: 406px;
    margin-bottom: 30px;

    background: $white-to-right;
      margin-left: 20px;
      margin-right: 20px;

      @include vp-1919 {
        max-width: 320px;
        margin-left: 15px;
        margin-right: 15px;
      }

      @include vp-1279 {
        max-width: 300px;
      }

      @include vp-1023 {

        margin-bottom: 0;
        width: 300px;
        // margin-left: 13px;
      }

      @include vp-767 {
        margin-left: 7.5px;
        margin-right: 7.5px;

      }

      @include vp-639 {
        max-width: 290px;
        overflow: hidden;
        // margin: 0 auto;
      }

    &__img-container {

      @include vp-1279 {
        height: 185px;
      }

      img {
        object-fit: cover;

        @include vp-1279 {
          height: 100%;
        }
      }
    }

    &__inner {
      padding: 18px;

      @include vp-1919 {
        padding: 16px;
      }
    }

    .title {
      max-width: 356px;
      margin-bottom: 13px;
      color: $color-default-black;

      @include vp-1919 {
        margin-bottom: 45px;
      }

      @include vp-767 {
        margin-bottom: 29px;
      }

    }

    &__info {
      display: flex;
      flex-direction: row-reverse;
      margin-bottom: 15px;
      color: $color-alizarin-red;

      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;

      @include vp-1919 {
        margin-bottom: 19px;
      }

      @include vp-1279 {
        margin-bottom: 5px;
      }

      svg {
        margin-right: 5px;
        // fill:red;
      }
    }

    &__content-wrapper {
      display: flex;
      justify-content: space-between;

      .link {
        svg {
          margin-left: 5px;
        }
      }
    }

    &__date {
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;

      color: $color-dark-gray;

      @include vp-1919 {
        font-size: 14px;
        line-height: 17px;
      }

      @include vp-1279 {
        font-size: 12px;
        line-height: 15px;
      }
    }
  }

  &__link {
    position: relative;
    display: flex;
    margin-top: -127px;

    padding-bottom: 18px;
    // z-index: 1;



    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 7px;
      height: 2px;

      background-color: $color-default-white;
      pointer-events: none;

      @include vp-1439 {
        display: none;
      }
    }

    &:before {
      width: 74.5vw;
      left: 0;

      @include vp-1919 {
        width: 76vw;
      }
    }

    &:after {
      width: 15.6vw;
      right: 0;

      @include vp-1919 {
        width: 10.5vw;
      }
    }

    @include vp-1919 {
      margin-top: -116px;
    }

    @include vp-1439 {
      max-width: 98%;
      order: 1;
      margin-top: 0;
      margin-bottom: 19px;
    }

    @include vp-1279 {
      margin-bottom: 15px;
      margin-right: 30px;
      max-width: 100%;
    }

    @include vp-1023 {
      // margin-top: 155px;
    }

    @include vp-639 {
      margin-right: 15px;
      margin-bottom: 0;
      order: 3;
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 7px;
      height: 2px;

      background-color: $color-default-white;
      pointer-events: none;

      @include vp-1439 {
        display: none;
      }
    }

    &:before {
      width: 74.5vw;
      left: 0;

      @include vp-1919 {
        width: 76vw;
      }
    }

    &:after {
      width: 15.6vw;
      right: 0;

      @include vp-1919 {
        width: 10.5vw;
      }
    }

    .link {
      margin-right: 16.5%;
      margin-left: auto;
      cursor: pointer;

      @include vp-1919 {
        margin-right: 12%;
        font-size: 14px;
        line-height: 17px;
      }

      @include vp-1439 {
        margin-right: 16%;
      }

      @include vp-1279 {
        margin-right: 0;
      }

      @include vp-639 {
        color: $color-default-black;

        &:hover,
        &:focus {
          color: $color-alizarin-red;
        }
      }
    }

    svg {
      margin-left: 7px;
    }
  }

  &__link:hover {
    .link::after {
      opacity: 1;
    }
  }

  &__pagination {
    display: none;

    @include vp-767 {
      position: absolute;
      display: inline-flex;
      top: 380px;
      left: 15px;

      .pagination__button {
        padding: 0;
        background-color: transparent;
        border: 0;
        cursor: pointer;

        &--rectangle {

          background-color: transparent;
          outline: none;
          cursor: pointer;
          border: 2px solid $color-default-black;
          transition: border $trans-600;

          &:hover,
          &:focus,
          &:active {
            border-color: $color-default-black;
            transition: opacity $trans-600;
            outline: none;
          }
        }

        &--lg {
          width: 20px;
          height: 12px;

          &:not(:first-child) {
            margin-left: 10px;
          }

          @include vp-1023 {
            width: 15px;
            height: 8px;
            border-width: 1px;
          }
        }

        &--active {
          background-color: $color-default-black;
          cursor: auto;
          pointer-events: none;
          transition: background-color $trans-600;

          &:hover,
          &:focus,
          &:active {
            border-color: $color-default-black;
            transition: border-color $trans-600;
          }
        }

      &:before,
      &:after {
        content: "";
        position: absolute;
        width: 100vw;
        height: 2px;

        background-color: $color-default-black;
      }

      &:before {
        left: 75px;
      }

      &:after {
        right: 75px;
      }
    }
  }
}
}
