.main-nav {
  @include vp-1279 {
    display: none;
    opacity: 0;
  }
}

@keyframes ani {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

.main-nav.is-active {
  @include vp-1279 {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    width: 100vw;
    height: 100vh;
    background-color: rgba($color: $color-default-black, $alpha: 0.5);
    overflow: auto;
    animation: ani 0.5s forwards;
  }
}

.main-nav__wrapper {
  @include vp-1279 {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 280px;
    min-height: 100vh;
    padding: 35px 0 40px 0;
    background-color: $color-default-white;
  }
}

.main-nav__close {
  display: none;

  @include vp-1279 {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    outline: none;
    box-sizing: border-box;
    background-color: $color-alizarin-red;
    cursor: pointer;
    transition: opacity 0.6s ease;

    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

.main-nav__close svg {
  fill: $color-default-white;
}

.main-nav a {
  font-size: 15px;
  line-height: 18px;
  font-weight: 700;
  color: $color-default-white;

  @include vp-1919 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-1439 {
    font-size: 12px;
    line-height: 15px;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-1279 {
    display: flex;
    flex-direction: column;
    padding: 0 20px 10px 20px;
    border-bottom: 1px solid $color-whisper;
  }
}

.main-nav__item {
  position: relative;
  margin-right: 24px;

  @include vp-1919 {
    margin-right: 20px;
  }

  @include vp-1439 {
    margin-right: 15px;
  }

  @include vp-1279 {
    margin-bottom: 6px;
    margin-right: 0;
  }
}


.main-nav__item span {
  color: $color-default-white;
  transition: color 0.6s ease;

  @include vp-1279 {
    color: $color-default-black;
  }
}

.main-nav__item span:hover {
  color: $color-alizarin-red;
}

.main-nav__item span:active {
  color: $color-alizarin-red;
  opacity: 0.8;
}

.main-nav__item--active span {
  color: $color-alizarin-red;
}

.main-nav__dropdown {
  z-index: 105;
}

.main-nav__dropdown-list {
  position: absolute;
  top: 35px;
  display: flex;
  flex-direction: column;
  min-width: 300px;
  padding: 15px;
  background-color: $color-night-rider;
  list-style: none;

  @include vp-1279 {
    position: relative;
    top: auto;
    min-width: auto;
    padding: 0 0 0 15px;
    background-color: transparent;
  }
}

.main-nav__dropdown-toggle {
  width: 12px;
  height: 7px;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  line-height: 0;
  color: $color-alizarin-red;
  cursor: pointer;
}

.main-nav__dropdown-toggle > .is-open {
  transform: rotate(180deg);
}

.main-nav__dropdown-item:not(:last-of-type) {
  margin-bottom: 10px;
}

.main-nav__dropdown-item a {
  transition: color 0.6s ease;

  @include vp-1279 {
    color: $color-zambezi;
  }
}

.main-nav__dropdown-item:hover a {
  color: $color-alizarin-red;
}

.main-nav__dropdown-item:active a {
  color: $color-alizarin-red;
  opacity: 0.8;
}

.main-nav__inner {
  display: none;

  @include vp-1279 {
    display: flex;
    flex-direction: column;
  }
}

//    

.main-nav__phone-box {
  @include vp-1279 {
    display: flex;
    padding: 20px 18px 5px 18px;
  }
}

.main-nav__phone-box svg {
  @include vp-1279 {
    margin: 7px 12px 0 0;
    fill: $color-alizarin-red;
  }
}

.main-nav__phone-box a {
  @include vp-1279 {
    font-size: 14px;
    line-height: 22px;
    color: $color-default-black;
  }
}

.main-nav__phone-box p {
  @include vp-1279 {
    margin: -3px 0 0 0;
    font-size: 10px;
    line-height: 14px;
    color: $color-aluminum;
  }
}

.main-nav__callback {
  @include vp-1279 {
    display: flex;
    padding: 6px 18px 20px 18px;
    border-bottom: 1px solid $color-whisper;
  }
}

.main-nav__callback a {
  @include vp-1279 {
    display: flex;
    align-items: center;
    min-height: 30px;
    padding: 3px 8px;
    background-color: $color-alizarin-red;
    transition: opacity 0.6s ease;

    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

.main-nav__callback svg {
  @include vp-1279 {
    margin-right: 11px;
    fill: $color-default-white;
  }
}

.main-nav__callback span {
  @include vp-1279 {
    font-weight: 600;
  }
}

.main-nav__location {
  @include vp-1279 {
    display: flex;
    padding: 20px 22px 10px 22px;
  }
}

.main-nav__location svg {
  @include vp-1279 {
    margin-right: 10px;
    color: $color-alizarin-red;
  }
}

.main-nav__city-wrapper p {
  @include vp-1279 {
    margin: 0;
    font-size: 10px;
    line-height: 14px;
    color: $color-aluminum;
  }
}

.main-nav__city-wrapper span {
  @include vp-1279 {
    display: block;
    margin-top: -1px;
    font-size: 12px;
    line-height: 15px;
    font-weight: 600;
    color: $color-default-black;
    text-decoration: underline;
    text-decoration-style: dashed;
    text-decoration-color: $color-alizarin-red;
    cursor: pointer;
  }
}

.main-nav__log-in {
  @include vp-1279 {
    padding: 10px 22px;
  }
}

.main-nav__log-in svg {
  @include vp-1279 {
    margin-right: 9px;
    fill: $color-alizarin-red;
  }
}

.main-nav__log-in a {
  @include vp-1279 {
    display: flex;
    align-items: center;
  }
}

.main-nav__log-in span {
  @include vp-1279 {
    font-weight: 600;
    color: $color-default-black;
  }
}

.main-nav__search-form {
  @include vp-1279 {
    padding: 10px 20px;
  }
}


.main-nav__search-form form {
  @include vp-1279 {
    display: flex;
    align-items: center;
    min-height: 44px;
    padding-left: 7px;
    border: 1px solid $color-whisper;
  }
}

.main-nav__search-form svg {
  @include vp-1279 {
    fill: $color-alizarin-red;
  }
}

.main-nav__search-form input {
  @include vp-1279 {
    max-width: 195px;
    padding: 0 10px;
    background-color: transparent;
    border: none;
    outline: none;

    &::placeholder {
      font-size: 12px;
      line-height: 15px;
      font-weight: 600;
      color: $color-zambezi;
    }
  }
}
